  /**
   * 放置卡牌
   * @param betArr {array}
   * @returns {Promise<*>}
   */
  betNFT(betArr) {
    let contract = this.actionWeb3.getContract('BNBLottery');
    let from = store.state.app.address;
    return contract.methods['bets'](betArr).send({from})
  },
  /**
   * 领取nft奖励
   * @param epoch {string} 周期编号
   * @param tokenId {string} nftId
   * @returns {Promise<*>}
   */
  claimNFT(epoch, tokenId) {
    let contract = this.actionWeb3.getContract('BNBLottery');
    let from = store.state.app.address;
    return contract.methods['claim'](epoch, tokenId).send({from})
  },
  /**
   * 获取农场池子数量
   * @returns {*}
   */
  getPoolLength() {
    let contract = this.dataWeb3.getContract('LPPool');
    return contract.methods['poolLength']().call()
  },
  /**
   * 获取指定农场数据
   * @param {String} id 指定id
   * @returns {*}
   */
  getPoolInfo(id) {
    let contract = this.dataWeb3.getContract('LPPool');
    return contract.methods['getPool'](id).call()
  },
  async getWETHPricePair() {
    return Fetcher.fetchPairData(WETH[BUSD.chainId], BUSD, provider).catch(() => {
      return Fetcher.fetchPairData(WETH[BUSD.chainId], USDT, provider);
    })
  },
  //获取Token价格，如果没有配置通过uniswap查询价格的地址就从oracle预言机查询价格
  async getTokenPrice(address, decimals) {
    let token = new Token(BUSD.chainId, address, decimals);
    let ethPricePair = await this.getWETHPricePair();
    if (WETH[BUSD.chainId].address === address || address === '0xae13d989daC2f0dEbFf460aC112a837C89BAa7cd') {
      let route = new Route([ethPricePair], WETH[BUSD.chainId]);
      return route.midPrice.toSignificant(6);
    } else {
      let WETHTokenPair = await Fetcher.fetchPairData(WETH[BUSD.chainId], token, provider);
      let route = new Route([WETHTokenPair, ethPricePair], token);
      return route.midPrice.toSignificant(6);
    }
  },
  async getPoolMobility(id, pool) {
    const USD_LIST = ["USDT", "USDC", "DAI", "BUSD"];
    let contract = this.dataWeb3.getContract('LPPool');
    let token0symbol = pool.token0symbol;
    let token1symbol = pool.token0symbol;
    let item0 = USD_LIST.find(item => item === token0symbol);
    if (item0 != null) {
      let priceInfo = await contract.methods['getPidPrice'](id).call();
      let s0 = priceInfo.tokenAmountMc / Math.pow(10, pool.token0decimals);
      return s0 * 2;
    }
    let item1 = USD_LIST.find(item => item === token1symbol);
    if (item1 != null) {
      let priceInfo = await contract.methods['getPidPrice'](id).call();
      let s1 = priceInfo.quoteTokenAmountMc / Math.pow(10, pool.token1decimals);
      return s1 * 2;
    }
    
    try {
      let token1Price = await this.getTokenPrice(pool.token1, pool.token1decimals);
      let priceInfo = await contract.methods['getPidPrice'](id).call();
      let lpTotalInQuoteToken = priceInfo.quoteTokenAmountMc  * 2;
      let mobility = token1Price * lpTotalInQuoteToken / Math.pow(10, pool.token1decimals);
      return mobility;
    } catch(e) {
      let token0Price = await this.getTokenPrice(pool.token0, pool.token0decimals);
      let priceInfo = await contract.methods['getPidPrice'](id).call();
      let lpTotalInQuoteToken = priceInfo.tokenAmountMc  * 2;
      let mobility = token0Price * lpTotalInQuoteToken / Math.pow(10, pool.token0decimals);
      return mobility;
    }
    
    // console.debug(id, '===================')
    // return 'undefined';
  },
  async getProfitRate(pid, pool) {
    let {token1decimals,  allocPoint} = pool;
    let averageBlockTime = getBlockDeltaTime();
    let contract = this.dataWeb3.getContract('LPPool');
    let p1 = contract.methods['detTokenPerBlock']().call();
    let p2 = contract.methods['totalAllocPoint']().call();
    //自己发行的代币可能在市场没有价格，所以定为初始发行价格
    // let p3 = this.getTokenPrice('0xFDC310Fde606CfC314c7CAa90eFdcB7E2096efca', 18);
    let p3 = Promise.resolve(1);
    let [rewardPerBlock, totalAllocPoint, defxPrice] = await Promise.all([p1, p2, p3]);
    //每个块产生的代币
    let rewardPer = rewardPerBlock * allocPoint / (totalAllocPoint * Math.pow(10, token1decimals));
    //年收益
    let annual_profit = rewardPer * 24 * (3600 / averageBlockTime) * 365 * defxPrice * 100;
    //流动性【最优】lpToken的总价值(USDT)
    let mobility = await this.getPoolMobility(pid, pool);
    //年化率
    let annual_rate = annual_profit / mobility;
    return {
      mobility,
      annual_rate
    }
  },

  /**
   * 获取当前账户在指定农场的质押数量和总收益
   * @param {String} farm_id 农场id
   * @param {String} user_address 账户地址
   * @returns {Promise<{all_profit: unknown, bet_amount: unknown}>}
   */
  getUserInfoInFarm(farm_id, user_address) {
    let contract = this.dataWeb3.getContract('LPPool');
    let p1 = contract.methods['getUserInfo'](farm_id, user_address).call();
    let p2 = contract.methods['pendingToken'](farm_id, user_address).call();
    return Promise.all([p1, p2]).then(([info, all_profit]) => {
      let {amount, rewardAmount} = info;
      return {betAmount: amount, all_profit, rewardAmount}
    })
  },
  /**
   * 获取农场授权状态
   * @param {String} user_address 钱包地址
   * @param {String} farm_address 农场地址
   * @returns {*}
   */
  getFarmState(user_address, farm_address) {
    let contract = this.dataWeb3.getContract('ERC20', farm_address);
    let LPPool_address = contractConfig['LPPool'].address[store.state.app.chainId];
    return contract.methods["allowance"](user_address, LPPool_address).call();
  },
  /**
   * 获取农场授权状态
   * @param {String} farm_address 农场地址
   * @returns {*}
   */
  approveFarm(farm_address) {
    let from = store.state.app.address;
    let LPPool_address = contractConfig['LPPool'].address[store.state.app.chainId];
    let contract = this.actionWeb3.getContract('ERC20', farm_address);
    return contract.methods["approve"](LPPool_address, maxUint256.toString(10)).send({from})
  },
  /**
   * 获取农场 LP余额
   * @param {String} user_address 钱包地址
   * @param {String} address 农场的lpToken
   */
